<template>
    
    <div class="goods"> 
        <!-- 获取dom对象 通过 v-el : name -->
        <!-- 取的时候就是this.$els.name 2.0废除-->
        <div class="menu-wrap" ref='menuWrap'>
            <ul>
                <li class="menu-item" v-for = '(value,key) in goods' :class = "{'current' : currentIndex === key }" @click='selectMenu(key,$event)'>
                    <span class="text">
                        <span class="icon" v-show = 'value.type > 0' :class = 'classMap[value.type]'></span>{{value.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrap" ref='foodsWrap'>
            <ul>
                <li v-for = 'item in goods' class="food-list food-list-hook">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li @click.stop.prevent = 'selectFood(food,$event)' v-for = 'food in item.foods' class="food-item">
                            <div class="icon">
                                <img :src="food.icon" alt="">
                            </div>
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span>月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <!-- 价格 原价和现价 -->
                                <div class="price">
                                    <span class="now">￥{{food.price}}</span>
                                    <span v-show = 'food.oldPrice' class="old">￥{{food.oldPrice}}</span>
                                </div>
                                <div class="cartcontrol-wrap">
                                    <cartcontrol :food = 'food'></cartcontrol>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div> 
        <shopcart :select-foods = 'selectFoods' :shop-cart = 'seller'></shopcart>
        <detail :food = 'selectedFood' ref = 'showdetail'></detail>
    </div>
</template>

<script>
    // 用法：https://www.npmjs.com/package/better-scroll
    import BScroll from 'better-scroll';
    import shopcart from '../shopcart/shopcart.vue';
    import cartcontrol from '../cartcontrol/cartcontrol.vue';
    import detail from '../detail/detail.vue';

    export default {
        props:{
            seller:{
                type:Object
            }
        },
        data () {
            return {
                goods: [] ,
                listHeight : [] ,
                scrollY : 0 ,
                selectedFood : {}
            };
        },
        components:{
            shopcart ,
            cartcontrol,
            detail
        },
        computed:{
            // 定义左侧当前索引在哪
            currentIndex(){
                for( let i = 0 ; i < this.listHeight.length ; i++ ){
                    let height1 = this.listHeight[i];
                    let height2 = this.listHeight[i+1];
                    if ( !height2 || (this.scrollY >= height1 && this.scrollY < height2) ){
                        return i;
                    }
                }
                return 0;
            },
            selectFoods(){
                let foods = [];
                // 遍历goods(商品列表)列表里面的每一个good(商品)
                this.goods.forEach( (good) => {
                    // 再遍历good(商品分类)里面的每一个foods
                    good.foods.forEach( (food) => {
                        // 然后判断food.count,如果大于0 则说明已经被选中了
                        if( food.count){
                            foods.push(food);
                        }
                    });
                });
                return foods;
            }
        },
        created(){
            this.classMap = ['decrease','discount','guarantee','special','invoice'],
            // this.$ajax.get('/api/goods').then((resp) => {
            //     // console.log(resp.data)
            //     if( resp.data.errno === 0 ){
            //         this.goods = resp.data.data;
            //         // 使用_initScroll()
            //         // 使用$nextTic进行 初始化 bsscroll  是一个回调函数
            //         this.$nextTick(() => {
            //             this._initScroll();
            //             // 计算每块的高度
            //             this.calcHeight();  
            //         })
            //         // console.log(this.goods+'66');
            //     }
            // }).catch(function(err){
            //     console.log(err);
            // });
            this.$ajax.get('../../../static/data.json').then((resp) => {
                // console.log(resp.data)
                // if( resp.data.errno === 0 ){
                    this.goods = resp.data.goods;
                    // 使用_initScroll()
                    // 使用$nextTic进行 初始化 bsscroll  是一个回调函数
                    this.$nextTick(() => {
                        this._initScroll();
                        // 计算每块的高度
                        this.calcHeight();  
                    })
                    // console.log(this.goods+'66');
                // }
            }).catch(function(err){
                console.log(err);
            });

        },
        // 一开始为什么不能滑动 那是因为BScroll在计算高度的时候 它是一个dom
        // vue里面更改数据 dom跟着映射 但是实际上vue更新dom的时候是异步更新数据的
        // 也就是说vue里面有一个叫做$nextTick的东西 它其实是在哪个里面进行执行dom更新 异步执行
        // 所以说我们虽然改变数据 它并不会有变化 dom没有变化 我们初始化的时候高度计算 就会有问题
        // 所以就需要用到vue的一个接口 $nextTick
        methods:{
            //methods对象有一个方法_initScroll
            _initScroll (){
                // 获取左侧栏的BScroll
                // 接收两个参数，第一个参数的dom对象 第二个参数是json对象
                // 这个是左侧菜单
                this.menuScroll = new BScroll( this.$refs.menuWrap , {
                    click: true 
                } );
                // 这个是右侧菜单
                this.foodsScroll = new BScroll( this.$refs.foodsWrap , {
                    probeType : 3,
                    click: true 
                    // 1 滚动的时候会派发scroll事件，会截流。2滚动的时候实时派发scroll事件，不会截流。 3除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件
                } );
                // better-scroll的事件
                this.foodsScroll.on('scroll',(pos) => {
                    // console.log(pos.x + '~' + pos.y)
                    // 绝对值取整
                    this.scrollY = Math.abs(Math.round(pos.y)); 
                    // console.log(Math.round(pos.y))
                })
            },
            calcHeight(){
                // 通过设置的类名获取到每一个li
                let foodList = this.$refs.foodsWrap.getElementsByClassName('food-list-hook');
                let height = 0 ;
                this.listHeight.push(height);
                // 循环把每一块的高度加在height里面 并且push进listHeight
                for( let i = 0 ; i < foodList.length ; i++ ){
                    height += foodList[i].clientHeight;
                    this.listHeight.push(height);
                }
                // console.log(this.listHeight)
            },
            // 点击没效果 因为better-scroll里面没设置
            // 但是在pc页面下 会colsole两次 因为better-scroll在pc页面下并不会阻止这个点击事件
            // 所以会被console两次 派发两次
            // 怎么去避免这个问题呢 
            // 获取selectMenu的时候 传进来 $event事件
            // 监视到event变量 就是click传递的event
            // better-scroll 事件 和 原生事件有点区别 有一个_constructed的属性区别 原生的没有这个属性、
            // 如果是原生的 就return 不执行
            selectMenu(k,e){
                if( !e._constructed){
                    return;
                }
                // console.log(k)
                let foodList = this.$refs.foodsWrap.getElementsByClassName('food-list-hook');
                // 定义滚动相应的元素 取到这个dom
                let el = foodList[k];
                this.foodsScroll.scrollToElement(el,300);
                // scrollToElement(el, time, offsetX, offsetY, easing)
                // 滚动到某个元素，el（必填）表示 dom 元素，time 表示动画时间，offsetX 和 offsetY 表示坐标偏移量，easing 表示缓动函数
            },
            // 点击查看详情
            selectFood( f , e ){
                if( !e._constructed ){
                    return;
                }
                this.selectedFood = f; 
                // console.log(f)
                this.$refs.showdetail.show();
            }
        }
    }
</script>

<style lang="less" type="text/less">
    @import '../../../static/less/color.less';
    .goods{
        position: absolute;
        display: flex;
        overflow: hidden;
        width: 100%;
        bottom:0.92rem;
        top:3.52rem;
        .menu-wrap{
            flex:0 0 1.6rem;//不扩展、不缩放
            width: 1.6rem;
            background: #f3f5f7;
           .menu-item{
                width: 1.12rem;
                height: 1.08rem;
                background: #f3f5f7;
                line-height: 0.28rem;
                font-weight: 200;
                font-size: 0.24rem;
                color:#2f363d;
                padding: 0 0.24rem;
                text-align: center;//垂直居中 不管有几行
                display: table;
                &.current{
                    background: white;
                    position: relative;
                    margin-top: -1px;
                    z-index: 10;
                    font-weight: 700;
                    .text{
                        border:none;
                    }
                }
                .text{
                    width: 1.12rem;
                    text-align: center;
                    display: table-cell;
                    vertical-align: middle;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .icon{
                        display: inline-block;
                        width: 0.34rem;
                        height: 0.34rem;
                        vertical-align: middle;
                        background-repeat: no-repeat;
                        background-size: 0.34rem 0.34rem;
                        &.decrease{
                            background-image: url('decrease.png');
                        }                                
                        &.discount{
                            background-image: url('discount.png');
                        }                                
                        &.guarantee{
                            background-image: url('guarantee.png');
                        }                                
                        &.special{
                            background-image: url('special.png');
                        }                                
                        &.invoice{
                            background-image: url('invoice.png');
                        }
                    }
                }
           }
        }
        .foods-wrap{
            flex:1;
            .food-list{

                .title{
                    width: 100%;
                    height: 0.52rem;
                    line-height: 0.52rem;
                    border-left: 2px solid #d9dde1;
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                    background: #f3f5f7;
                    padding-left: 0.288rem;
                }
                .food-item{
                    display: flex;
                    margin:0.36rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    padding-bottom: 0.36rem;
                    position: relative;
                    .icon{
                        flex:0 0 1.51rem;
                        margin-right: 0.1rem;
                        img{
                            width: 1.15rem;
                            height: 1.15rem;
                        }
                    }
                    .content{
                        flex:1;
                        .name{
                            font-size: 0.28rem;
                            color:rgb(7,17,27);
                            line-height: 0.28rem;
                            margin-top: 0.04rem;
                        }
                        .desc{
                            margin: 0.16rem 0 0.16rem 0;
                            color: #888;
                        }
                        .extra{
                            color:#888;
                        }
                        .price{
                            font-weight: 700;
                            margin-top: 0.1rem;
                            .now{
                                margin-right: 0.16rem; 
                                font-size: 0.28rem;
                                color:red;
                            }
                            .old{
                                text-decoration: line-through;
                                font-size: 0.2px;
                                color:#8b9298;
                            }
                        }
                        .cartcontrol-wrap{
                            position: absolute;
                            right: 0;
                            bottom:0.1rem;
                        }
                    }
                }
            }
        }
    }
</style>



