<template>
  <div class="star" @click="show">
    
    <span v-for = 'itemClass in itemList' :class = 'itemClass' class = 'star-item'></span>

  </div>
</template>

<script>
    const LENGTH = 5;
    const ICS_ON = 'on'
    const ICS_HALF = 'half'
    const ICS_OFF = 'off'
    export default {
        props : ['score'],
        //计算属性用来计算score并且实施展示星星
        computed:{
            itemList(){
                let result = [];
                let score = Math.floor(this.score*2)/2;
                let hasLitter = Number.isInteger(score);//es6判断一个数是不是整数
                let integer = Math.floor(score); //取整数 
                for ( let i = 0 ; i < integer ; i++ ){
                    result.push(ICS_ON);//多少个整数就加多少个星星
                }
                if ( !hasLitter ){
                    result.push(ICS_HALF);  //  如果有大于0.5的小数  就加一个半星
                }
                while ( result.length < LENGTH ){
                    result.push( ICS_OFF )  //长度不足  就加一个空心的
                }
                return result;
            }
        },
        methods:{
            show(){
                console.log(this.itemList);
            }
        }
    }
</script>

<style lang="less" type="text/less">
  .star{
    .star-text{
        font-size: 0.4rem;
    }
    .star-item{
        display: inline-block;
        // background-repeat: no-repeat;
        width: 0.3rem;
        height: 0.3rem;
        margin-right: 0.1rem;
        &.on{
            background-image: url('star_on.png');
            background-size: 0.3rem 0.3rem;
            background-repeat: no-repeat;
        }        
        &.half{
            background-image: url('star_half.png');
            background-size: 0.3rem 0.3rem;
            background-repeat: no-repeat;
        }        
        &.off{
            background-image: url('star_off.png');
            background-size: 0.3rem 0.3rem;
            background-repeat: no-repeat;
        }
    }
   
  }
</style>



