<template>
    <div class="sellers" ref = 'sellerwraps'>
        <div class="seller-content">
            <!-- 概述 -->
            <div class="overview">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc">
                    <star :score = 'seller.score'></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">{{seller.sellCount}}</span>
                </div>
                <ul class="remark">
                    <li class="block">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>                   
                    <li class="block">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>                   
                    <li class="block">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                </ul>
            </div>
            <split></split>
            <!-- 公告 -->
            <div class="bulletin">
                <h1 class="title">公告与活动</h1>
                <div class="content-wrap">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
                <ul v-if='seller.supports' class="supports">
                    <li class="support-item" v-for = '(value, key) in seller.supports'>
                        <span class="icon" :class='classMap[seller.supports[key].type]'></span>
                        <span class="text">{{seller.supports[key].description}}</span>
                    </li>
                </ul>
            </div>
            <split></split>
            <div class="pics">
                <div class="title">商家实景</div>
                <div class="pic-wrap" ref = 'picwrap'>
                    <ul class="pic-list" ref = 'piclist'>
                        <li class="pic-item" v-for = 'pic in seller.pics'>
                            <img :src="pic" width="120" height="90" >
                        </li>
                    </ul>
                </div>
            </div>
            <split></split>
            <div class="info">
                <h1 class="title">商家信息</h1>
                <ul>
                    <li class="info-item" v-for = 'info in seller.infos'>{{info}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>    
    import star from '../star/star.vue';
    import split from '../split/split.vue';
    import BScroll from 'better-scroll';
    
    export default { 
        props : {
            seller : Object
        },
        components : {
            star ,
            split
        },
        created(){
            this.classMap = ['decrease','discount','guarantee','special','invoice'];
        },
        watch : {
            seller(){
                // console.log(4);
                this._initScroll();
                this._initPicScroll();  
            }
        },
        methods : {
            _initScroll(){
                this.$nextTick( () => {
                    if ( !this.scroll ) {
                        this.scroll = new BScroll( this.$refs.sellerwraps ,{
                            click : true
                        });
                    } else {
                        this.scroll.refresh();
                    }
                })
            },
            // 计算商家图片的宽度
            _initPicScroll(){
                if( this.seller.pics ){
                    let picWidth = 120;
                    let margin = 6;
                    let width = ( picWidth + margin ) * this.seller.pics.length - margin;
                    this.$refs.piclist.style.width = width + 'px';
                    this.$nextTick( () => {
                        this.picScroll = new BScroll( this.$refs.picwrap , {
                            scrollX : true,
                            // eventPassthrough : 'vertical'
                        });
                    });
               }
            }
        },
        // 执行时机早于watch
        mounted(){
            // console.log(5)
            this._initScroll();
            this._initPicScroll();
        }
    }

</script>

<style lang="less" type="text/less">
    @import '../../../static/less/color.less';
    .sellers{
        position: absolute;
        top: 3.48rem;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;  
        .overview{
            padding: 0.36rem;
            .title{
                margin-bottom: 0.16rem;
                line-height: 0.28rem;
                color: rgb(7,17,27);
                font-size: 0.28rem;
            }
            .desc{
                padding-bottom: 0.36rem;
                line-height: 0.36rem;
                font-size: 0;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .star{
                    display: inline-block;
                    margin-right: 0.16rem;
                    vertical-align: top;
                }
                .text{
                    display: inline-block;
                    vertical-align: top;
                    margin-right: 0.224rem;
                    font-size: 0.2rem;
                    color: rgb(77,85,93);
                }
            }
            .remark{
                display: flex;
                padding-top: 0.36rem;
                .block{
                    flex:1;
                    text-align: center;
                    border-right: 1px solid rgba(7,17,27,0.1);
                    &:last-child{
                        border-right: none;
                    }
                    h2{
                        margin-bottom: 0.16rem;
                        line-height: 0.2rem;
                        font-size: 0.2rem;
                        color: rgb(147,153,159);
                    }
                    .content{
                        line-height: 0.48rem;
                        font-size: 0.2rem;
                        color: rgb(7,17,27);
                        .stress{
                            font-size: 0.48rem;
                        }
                    }
                }
            }
        }
        .bulletin{
            padding: 0.36rem 0.36rem 0 0.36rem;
            .title{
                margin-bottom: 0.16rem;
                line-height: 0.28rem;
                color: rgb(7,17,27);
                font-size: 0.28rem;
            }
            .content-wrap{
                padding: 0 0.24rem 0.32rem 0.24rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .content{
                    line-height: 0.48rem;
                    font-size: 0.24rem;
                    color: rgb(240,20,20);
                }
            }
            .supports{
                .support-item{
                    padding: 0.32rem 0.24rem;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    &:last-child{
                        border-bottom: none;
                    }
                    font-size: 0;
                    .icon{
                        width: 0.36rem;
                        height: 0.36rem;
                        margin-right: 0.14rem;
                        display: inline-block;
                        background-repeat: no-repeat;
                        background-position: center center;
                        background-size: 0.36rem 0.36rem;
                        vertical-align: top;
                        &.decrease{
                            background-image: url('decrease.png');
                        }                                
                        &.discount{
                            background-image: url('discount.png');
                        }                                
                        &.guarantee{
                            background-image: url('guarantee.png');
                        }                                
                        &.special{
                            background-image: url('special.png');
                        }                                
                        &.invoice{
                            background-image: url('invoice.png');
                        }
                    }
                    .text{
                        line-height: 20px;
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                    }
                }
            }
        }
        .pics{
            padding: 0.36rem;
            .title{
                margin-bottom: 0.16rem;
                line-height: 0.28rem;
                color:rgb(7,17,27);
                font-size: 0.28rem;
            }
            .pic-wrap{
                width: 100%;
                overflow: hidden;
                white-space: nowrap;
                .pic-list{
                    font-size: 0;
                    .pic-item{
                        display: inline-block;
                        margin-right: 0.12rem;
                        width: 120px;
                        height: 90px;
                        &:last-child{
                            margin: 0;
                        }
                    }
                }
            }
        }
        // 商家信息
        .info{
            padding: 0.36rem 0.36rem 0 0.36rem;
            .title{
                padding-bottom: 0.24rem;
                line-height: 0.28rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                color: rgb(7,17,27);
                font-size: 0.28rem;
            }
            .info-item{
                padding: 0.32rem 0.24rem;
                line-height: 0.32rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                font-size: 0.24rem;
                &:last-child{
                    border-bottom: none;
                }
            }
        }
    }
</style>



