<template>
    <div class="ratings" ref = 'ratingswrap'>
        <div class="ratings-content">
            <div class="overview">
                <!-- 综合评分 -->
                <div class="view-left">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}%</div>
                </div>
                <div class="view-right">
                    <!-- 星星组件 -->
                    <div class="score-wrap">
                        <span class="title">服务态度</span>
                        <star :size = '36' :score = 'seller.serviceScore'></star>
                        <span class="score">{{ seller.serviceScore }}</span>
                    </div>
                    <div class="score-wrap">
                        <span class="title">商品评价</span>
                        <star class='star' :score = 'seller.foodScore'></star>
                        <span class="score">{{ seller.foodScore }}</span>
                    </div>
                    <!-- 送达时间 -->
                    <div class="delivery-wrap">
                        <span class="title">送达时间</span>
                        <span class="delivery">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <!-- 分割线 -->
            <split></split>
            <!-- 评价选择列表 -->
            <ratingselect :select-type = 'selectType' :only-content = 'onlyContent' :desc = 'desc' :ratings = 'ratings'></ratingselect>
            <!-- 评价列表 -->
            <div class="rating-wrap">
                <ul>
                    <li v-show = 'needShow(rating.rateType , rating.text)' v-for = 'rating in ratings' class="rating-item">
                        <!-- 头像 -->
                        <div class="avatar">
                            <img :src="rating.avatar" alt="" width='56px' height='56px'>
                        </div>
                        <!-- 内容 -->
                        <div class="content">
                            <h1 class = 'name'>{{rating.username}}</h1>
                            <div class="star-wrap">
                                <star :score = 'rating.score'></star>
                                <span class="delivery" v-show = 'rating.deliveryTime'>{{rating.deliveryTime}}</span>
                            </div>
                            <p class="text">{{rating.text}}</p>
                            <div class="recommend" v-show = 'rating.recommend && rating.recommend.length'>
                                <span>
                                    <i v-show = 'rating.rateType === 0' class="icon iconfont icon-damuzhion cc"></i>
                                    <i v-show = 'rating.rateType === 1' class="icon iconfont icon-down dd"></i>
                                </span>
                                <span class="item" v-for = 'item in rating.recommend'>{{item}}</span>
                            </div>
                            <div class="time">
                                {{rating.rateTime | getTimes}}
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>

    import star from '../star/star.vue';
    import split from '../split/split.vue';
    import BScroll from 'better-scroll';
    import ratingselect from '../ratingselect/ratingselect.vue';
    import bus from '../../../static/js/eventBus.js';


    const POSITIVE = 0; // 正面评价
    const NEGATIVE = 1; // 负面评价
    const ALL = 2;      // 所有评价 

    export default {
        props : {
            seller : {
                type : Object
            }
        },
        data(){
            return {
                ratings : [] ,
                showDetail : false,
                selectType : ALL,
                onlyContent : true,
                desc : {
                    all : '全部',
                    positive : '推荐',
                    negative : '吐槽',
                }
            }
        },
        filters : {
            getTimes(date){
                var time = new Date(date);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                return (y+"-"+m+"-"+d+" "+h+":"+mm+":"+s);
            }
        },
        components : {
            star ,
            split ,
            ratingselect  

        },
        mounted (){
            console.log(3)
        },
       // mounted: function () {
       //      console.group('created 创建完毕状态===============》');
       //      console.log("%c%s", "color:red","el     : " + this.$el); //undefined
       //         console.log("%c%s", "color:red","data   : " + this.$data); //已被初始化 
       //         console.log("%c%s", "color:red","message: " + this.message); //已被初始化
       //  },
        methods : {
                needShow(type,text){
                // 如果判断 onlyContent（只有内容）  并且没有文字评论 return false
                // 意思就是想显示内容 但是又没有内容 肯定返回false
                // 否则他显示所有的
                if( this.onlyContent && !text ){
                    return false;
                };


                // 如果当前的类型是所有的 那么就显示
                if( this.selectType === ALL ){
                    return true;
                    this.$nextTick( () => {
                        this.scroll.refresh();
                    })
                }else{
                    // 否则 判断当前的type 是不是和selectType相等
                    // 如果相等 return true 否则 return false
                    return type === this.selectType;
                    this.$nextTick( () => {
                        this.scroll.refresh();
                    })
                }
            }
        },
        created(){
            this.$ajax.get('../../static/data.json').then((resp) => {
                // console.log(resp.data)
                // if( resp.data.errno === 0 ){
                    this.ratings = resp.data.ratings;
                    this.$nextTick( () => {
                        // console.log(this.$refs.ratingswrap);
                        // this.scroll = new BScroll( this.$refs.ratingswrap ,{
                        //     click : true
                        // });
                        if( !this.scroll ){
                            this.scroll = new BScroll( this.$refs.ratingswrap ,{
                                click : true
                            });
                        }else{
                            this.scroll.refresh();
                        }
                    })
                    // 使用_initScroll()
                    // 使用$nextTic进行 初始化 bsscroll  是一个回调函数
                    // this.$nextTick(() => {
                    //     this._initScroll();
                    //     // 计算每块的高度
                    //     this.calcHeight();  
                    // })
                    // console.log(this.goods+'66');
                // }
            }).catch(function(err){
                console.log(err);
            });
            // this.$ajax.get('/api/ratings').then((resp) => {
            //     // console.log(resp.data)
            //     if( resp.data.errno === 0 ){
            //         this.ratings = resp.data.data;
            //         this.$nextTick( () => {
            //             // console.log(this.$refs.ratingswrap);
            //             // this.scroll = new BScroll( this.$refs.ratingswrap ,{
            //             //     click : true
            //             // });
            //             if( !this.scroll ){
            //                 this.scroll = new BScroll( this.$refs.ratingswrap ,{
            //                     click : true
            //                 });
            //             }else{
            //                 this.scroll.refresh();
            //             }
            //         })
            //         // 使用_initScroll()
            //         // 使用$nextTic进行 初始化 bsscroll  是一个回调函数
            //         // this.$nextTick(() => {
            //         //     this._initScroll();
            //         //     // 计算每块的高度
            //         //     this.calcHeight();  
            //         // })
            //         // console.log(this.goods+'66');
            //     }
            // }).catch(function(err){
            //     console.log(err);
            // });

            bus.$on('selectType-click', type => {
                this.selectType = type;
                // console.log(type)
                this.$nextTick( () => {
                    this.scroll.refresh();
                })
            });

            bus.$on('onlyContent-click', cc => {
                // console.log(cc)
                this.onlyContent = cc;
                this.$nextTick( () => {
                    this.scroll.refresh();
                })
            })

        
        }
    }
          
</script>

<style lang="less" type="text/less">
    @import '../../../static/less/color.less';
    .ratings{
        position: absolute;
        top: 3.48rem;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;    
        .overview{
            display: flex;
            padding:0.36rem 0;
            .view-left{
                flex:0 0 2.75rem;
                width: 2.75rem;
                text-align: center;
                padding: 0.12rem;
                border-right: 1px solid rgba(7,17,27,0.1);
                .score{
                    line-height: 0.56rem;
                    font-size: 0.48rem;
                    color:rgb(255,153,0);
                    margin-bottom: 0.12rem;
                }
                .title{
                    line-height: 0.24rem;
                    font-size: 0.24rem;
                    color: rgb(7,17,27);
                    margin-bottom: 0.16rem;
                }
                .rank{
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                    font-size: 0.2rem;
                }
            }
            .view-right{
                // flex:1;
                padding: 0.12rem 0 0.12rem 0.36rem;
                .score-wrap{
                    line-height: 0.36rem;
                    margin-bottom: 0.16rem;
                    font-size: 0;
                    .title{
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        line-height: 0.36rem;
                    }
                    .star{
                        display: inline-block;
                        vertical-align: top;
                        margin: 0 0.2rem;
                    }
                    .score{
                        display: inline-block;
                        vertical-align: top;
                        font-size: 0.24rem;
                        line-height: 0.36rem;
                        color:rgb(255,153,0);
                    }
                }
                .delivery-wrap{
                    font-size: 0;
                    .title{
                        font-size: 0.24rem;
                        line-height: 0.36rem;
                        color:rgb(7,17,27);
                    }
                    .delivery{
                        margin-left: 0.24rem;
                        font-size: 0.24rem;
                        color:rgb(147,153,159);
                    }
                }
            }
        }   
        .rating-wrap{
            padding: 0 0.36rem;
            .rating-item{
                display: flex;
                padding: 0.36rem 0;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                .avatar{
                    flex: 0 0 1rem;
                    width: 1rem;
                    margin-right: 0.24rem;
                    img{
                        border-radius: 50%;
                    }
                }
                .content{
                    position: relative;
                    flex:1;
                    .name{
                        line-height: 0.24rem;
                        font-size: 0.2rem;
                        color:rgb(7,17,27);
                        margin-bottom: 0.08rem;
                    }
                    .star-wrap{
                        margin-bottom: 0.12rem;
                        font-size: 0;
                        .star{
                            display: inline-block;
                            margin-right: 0.12rem;
                            vertical-align: top;
                        }
                        .delivery{
                            display: inline-block;
                            vertical-align: top;
                            line-height: 0.36rem;
                            font-size: 0.2rem;
                            color:rgb(147,153,159);
                        }
                    }
                    .text{
                        line-height: 0.36rem;
                        margin-bottom: 0.16rem;
                        color: rgb(7,17,27);
                        font-size: 0.24rem;
                    }
                    .recommend{
                        line-height: 0.32rem;
                        font-size: 0;
                        i{
                            font-size: 0.18rem;
                            margin-right: 0.2rem;
                            &.cc{
                                color:rgb(0,160,220);
                            }
                            &.dd{
                                color:rgb(147,153,159);
                            }
                        }
                        .item{
                            padding: 0 0.12rem;
                            margin-right: 0.2rem;
                            border: 1px solid rgba(7,17,27,0.1);
                            border-radius: 0.02rem;
                            color: rgb(147,153,159);
                            background: #fff;
                            font-size: 0.2rem;
                        }
                    }
                    .time{
                        position: absolute;
                        top:0;
                        right: 0;
                        line-height: 0.24rem;
                        font-size: 0.2rem;
                        color:rgb(147,153,159);
                    }
                }
            }
        }
    }
</style>



