<template>
    <div class="cartcontrol">
        <!-- 减少 -->
        <transition name='move'>
    		<div class="cart-reduce" v-show='food.count > 0' @click.stop.prevent = 'reduceCart( $event )'>
    			<i class="icon iconfont icon-jianhao"></i>
    		</div>
        </transition>  
		<div class="cart-count" v-show='food.count > 0'>{{food.count}}</div>
        <!-- 添加 -->
		<div class="cart-add" @click.stop.prevent = 'addCart( $event )'>
			<i class="icon iconfont icon-jiahao"></i>
		</div>
    </div> 
</template>

<script>   
    import Vue from 'vue';

    export default {
    	props : {
    		food : {
 				type : Object
    		}
    	},
    	created(){
    		// console.log(this.food);
    	},
    	methods : {

    		addCart( event ){
                if( !event._constructed ){
                    return;
                };
                // console.log(7);
                // vue.js的特性：当你做添加或者删除某个不存在的字段是不可以的，
                // 它是（define.proto...）检测不出来这种变化，想要在添加或者删除成功，需要调用vue的一个接口

                if( !this.food.count ){
                    // 给this.food 添加一个 count的属性 同时设置值为1
                    Vue.set( this.food , 'count'  , 1 );
                }else{
                    this.food.count ++ ;
                }
            },
            reduceCart( event ){
    			if( !event._constructed ){
    				return;
    			};
    			// console.log(7);
    			// vue.js的特性：当你做添加或者删除某个不存在的字段是不可以的，
    			// 它是（define.proto...）检测不出来这种变化，想要在添加或者删除成功，需要调用vue的一个接口

    				this.food.count -- ;
    		}
    	}

    }
 
</script>

<style lang="less" type="text/less">

  	.cartcontrol{
  		font-size: 0;
        // 下面是加减移动动画            
        .move-enter-active, .move-leave-active {
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        // count>0时候的动画，v-show显示时的动画效果
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0
        }
  		.cart-reduce{
  			display: inline-block;
			padding: 0.12rem;
                width: 0.36rem;
            i{
                font-size: 0.36rem; 
                line-height: 0.36rem;   
                color:#00a0dc;
            }
  		}
  		.cart-count{
  			display: inline-block;
            font-size: 0.2rem;
            padding: 0.12rem;
            vertical-align: top;
            text-align: center;
            color: gray;
            width: 0.24rem;
            line-height: 0.36rem;
  		}
  		.cart-add{
  			display: inline-block;
            padding: 0.12rem;
  			i{
				font-size: 0.36rem;		
				line-height: 0.36rem;
				color:#00a0dc;
			}
  		}
  	}

</style>



