<template>
    <div class="shopcart">
        <div class="content" @click = 'toggleList()'>
            <div class="content-left">
                <div class="logo-wrap">
                    <div class="logo" :class="{'highlight' : totalCount > 0}">
                        <i class="icon iconfont icon-gouwuche" :class="{'highlight' : totalCount > 0}"></i>
                    </div>
                    <div class="num" v-show = 'totalCount > 0'>{{totalCount}}</div>
                </div>
                <div class="price" :class="{'highlight' : totalCount > 0}">￥{{totalPrice}}</div>
                <div class="desc">另需配送费￥{{shopCart.deliveryPrice}}元</div>
            </div>
            <div class="content-righ" @click.stop.prevent = 'pay'>
                <div class="pay" :class="payClass">
                    {{payShow}}
                </div>
            </div>
        </div>
        <!-- 购物车详情 -->
        <transition name = 'movetop'>
            <div class="shopcart-list" v-show = 'listShow'>
                <div class="list-header">
                    <h1 class="title">购物车</h1>
                    <span class = 'empty' @click = 'empty()'>清空</span>
                </div>
                <div class="list-content" ref='listContent'>
                    <ul>
                        <li class="food" v-for = 'food in selectFoods'>
                            <span class="name">{{food.name}}</span>
                            <div class="price">￥{{ food.price * food.count }}</div>
                            <div class="cartcoltrol-wrap">
                                <cartcoltrol :food = 'food'></cartcoltrol>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </transition>
        <transition name = 'mask'>
            <div class="list-mask" v-show = 'listShow' @click = 'hidelist()'></div>
        </transition>
    </div>
</template>

<script>   
    import BScroll from 'better-scroll';
    import cartcoltrol from '../cartcontrol/cartcontrol.vue';
    export default {
        props:{
            shopCart : {
                type : Object    
            },
            // 在vue中 如果你的props 是Array 或者 Object 
            // 那么default就是一个函数
            selectFoods:{
                type:Array,
                default(){
                    return [{
                        price:1,
                        count:4
                    }];
                }
            },
        },
        data (){
            return {
                // 购物车是否折叠
                fold : true
            }
        },
        components : {
            cartcoltrol
        }
        ,
        computed:{
            // 计算总价
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach( (food) => {
                    total += food.price * food.count;
                });
                return total ;
            },
            // 计算总数量
            totalCount(){
                let count = 0;
                this.selectFoods.forEach( (food) => {
                    count += food.count;
                });
                // console.log(count);
                return count;
            },
            // 计算起送价格
            payShow(){
                if ( this.totalPrice === 0 ){

                    return '￥'+this.shopCart.minPrice+'元起送';

                } else if ( this.shopCart.minPrice > this.totalPrice ){

                    let diff = this.shopCart.minPrice - this.totalPrice;

                    return '还差￥' + diff + '元起送';

                } else {

                    return '去结算';

                }
            },
            // 计算运送价格的背景颜色
            payClass(){
                if ( this.totalPrice < this.shopCart.minPrice ){
                    return 'not-enough'
                } else {
                    return 'enough';
                }
            },
            listShow(){
                if ( !this.totalCount ){
                    this.fold = true;
                    return false;
                }
                let show = !this.fold;
                // 当列表展示的时候给BScroll做初始化
                // 数据变了，dom的变化并没有立即生效，而BScroll是严重依赖于dom 所以要在$nextTick里面写
                if( show ){
                    this.$nextTick( () => {
                        if( !this.scroll ){
                            this.scroll = new BScroll( this.$refs.listContent , {
                                click : true
                            });
                        }else{
                            this.scroll.refresh();
                        }
                    });
                }
                return show;
            }
        },
        methods : {
            // 显示购物车列表
            toggleList(){
                if( !this.totalCount ){
                    return;
                }
                this.fold = !this.fold;
            },
            // 清空列表
            empty(){
                this.selectFoods.forEach( (food) => {
                    food.count = 0;
                })
            },
            // 隐藏购物车列表
            hidelist(){
                this.fold = true;
            },
            // 去结算
            pay(){
                if( this.totalPrice < this.shopCart.minPrice ){
                    return;
                }
                window.alert(`支付${ this.totalPrice + this.shopCart.deliveryPrice}元`);
            }
        }
    };

</script>

<style lang="less" type="text/less">
    .shopcart{
        height:0.96rem;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 50;
        .content{
            display: flex;
            background: #141d27;
            // font-size: 0;
            .content-left{
                flex:1;   
                .logo-wrap{
                    display: inline-block;
                    vertical-align: top;
                    position: relative;
                    top: -0.2rem;
                    margin-left: 0.24rem;
                    margin-bottom: 0.04rem;
                    padding:0.12rem;
                    background: #141d27;
                    border-radius: 50%;
                    box-sizing: border-box;
                    .logo{
                        width: 0.88rem;
                        height: 0.88rem;
                        line-height: 0.88rem;
                        text-align: center;
                        // background: white;
                        background-color: #2b343c;
                        &.highlight{
                            background:#00a0dc;
                        }
                        border-radius: 50%;
                        i{
                            font-size: 0.46rem;
                            color:rgba(255,255,255,0.4);
                            &.highlight{
                                color: white;
                            }
                        }
                    }
                    .num{
                        position: absolute;
                        top:0;
                        right: 0;
                        width: 0.48rem;
                        height: 0.32rem;
                        line-height: 0.37rem;
                        text-align: center;
                        border-radius: 0.16rem;
                        font-size: 0.09rem;
                        font-weight: 700;
                        color:white;
                        background: rgb(240,20,20);
                        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
                    }
                }   
                .price{
                    display: inline-block;
                    font-size: 0.48rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.24rem;
                    box-sizing: border-box;
                    padding-right: 0.24rem;
                    border-right: 1px solid rgba(255,255,255,0.1);
                    &.highlight{
                        color: #fff;
                    }
                }   
                .desc{
                    display: inline-block;
                    font-size: 0.30rem;
                    font-weight: 300;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.28rem;
                    box-sizing: border-box;
                    padding-left: 0.24rem;
                }           
            }
            .content-righ{
                flex:0 0 2.05rem;
                width: 2.05rem;
                .pay{
                    font-size: 0.24rem;
                    font-weight: 700;
                    line-height: 0.98rem;
                    height: 0.96rem;
                    color:#26333b;
                    background-color: #2b343c;
                    color:rgba(255,255,255,0.4);
                    text-align: center;
                    &.not-enough{
                        background: #2b333b;
                    }
                    &.enough{
                        background: #00b43c;
                        color: #fff;
                    }
                }
            }
        }
        // 购物车出入动画
        .movetop-enter-active, .movetop-leave-active {
            transition: all 0.8s;
            opacity: 1;
        }
        .movetop-enter, .movetop-leave-active {
            transform:translate(0,6rem);
            opacity: 0;
        }
        .shopcart-list{
            top:-5rem;
            position: absolute;
            left: 0;
            height: 6rem;
            z-index: -1;
            width: 100%;
            background: #fff;
            .list-header{
                height: 0.8rem;
                background: #f3f5f7;
                line-height: 0.8rem;
                border-bottom: 1px solid rgba(7,17,27,0.1);
                padding: 0 0.36rem;
                font-weight: 200;
                overflow: hidden;
                .title{
                    float: left;
                    font-size: 0.28rem;
                }
                .empty{
                    float: right;
                    font-size: 0.24rem;
                    color:rgb(0,160,220);
                }
            }
            .list-content{
                padding: 0 0.36rem;
                max-height: 4.34rem;
                // background: #fff;
                overflow: hidden;
                .food{
                    position: relative;
                    padding: 0.24rem 0;
                    box-sizing: border-box;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .name{
                        line-height: 0.48rem;
                        font-size: 0.28rem;
                        color:rgb(7,17,27);
                    }
                    .price{
                        position: absolute;
                        right:1.8rem;
                        bottom:0.24rem;
                        line-height: 0.3rem;
                        font-size: 0.28rem;
                        font-weight: 700;
                        color:rgb(240,20,20);
                    }
                    .cartcoltrol-wrap{
                        position: absolute;
                        right: 0;
                        bottom: 0.12rem;
                    }
                }
            }
        }
    }
    .mask-enter-active, .mask-leave-active {
            transition: all 0.8s;
            opacity: 1;
        }
    .mask-enter, .mask-leave-active {
            opacity: 0;
        }
    .list-mask{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        background:rgba(7,17,27,0.6);
    }
</style>



