                        <span class="text">{{seller.supports[key].description}}</span>
                    </li>
                </ul>
                <!-- 下面是商家公告 -->
                <div class="title">
                    <div class="line"></div>
                    <div class="text">商家公告</div>
                    <div class="line"></div>
                </div>
                <div class="adv">
