    import star from '../star/star.vue'
    export default {
        created(){
            this.classMap = ['decrease','discount','guarantee','special','invoice'];
        },
        props : {
            seller:{
                // type : Object
            }
        },
        data(){
            return {
                detailShow:false
            }
        },
        components:{
            'v-star':star
        }
    }
</script>

