
<template>
	<div class="ratingselect">
		<div class="rating-type">
			<span @click = 'select(2,$event)' class="block positive" :class = "{ 'active' : selectType2 === 2}">{{ desc.all }}<span class="count">{{ ratings.length }}</span></span>			
			<span @click = 'select(0,$event)' class="block positive" :class = "{ 'active' : selectType2 === 0}">{{ desc.positive }}<span class="count">{{ positives.length }}</span></span>			
			<span @click = 'select(1,$event)' class="block negative" :class = "{ 'active' : selectType2 === 1}">{{ desc.negative }}<span class="count">{{ negatives.length }}</span></span>			 
		</div>
		<div class="switch" :class = "{ 'on' : onlyContent2 === false}">
			<span class="cc" @click = 'toggleContent($event)'>
                <i class="icon iconfont icon-duihao"></i>
			</span>
			<span class="title">只看有内容的评价</span>
		</div>
	</div>
</template>

<script>
	//定义三个常量
	const POSITIVE = 0;	// 正面评价
	const NEGATIVE = 1; // 负面评价
	const ALL = 2;		// 所有评价


    import bus from '../../../static/js/eventBus.js';
	

   	export default {

   		computed : {
   			positives(){
   				// 使用js中的迭代函数filter 
   				// 在使用filter时需要注意的是，前面调用的是需要使用filter的数组，
   				// 而给filter函数传入的是数组中的每个item，也就是说filter里面的函数，
   				// 是每个item要去做的，并将每个结果返回。
            
   				return this.ratings.filter( item => {
   					return item.rateType === POSITIVE
   				});
   			},
   			negatives(){
   				return this.ratings.filter( item => {
   					return item.rateType === NEGATIVE
   				});
   			},
   			selectType2(){
   				return this.selectType;
   			},
   			onlyContent2(){
   				return !this.onlyContent;
   				
   			}
   		},
   		props : {
   			ratings : {
   				//数组的对象需要返回一个函数
   				type : Array,
   				default () {
   					return [];
   				}
   			},
   			// 评价选择
   			selectType : {
   				type : Number,
   				default : ALL
   			},
   			onlyContent : {
   				type : Boolean,
   				default : false
   			},
   			desc : {
   				type : Object,
   				default () {
   					return {
   						all : '全部',
   						positive : '满意',
   						negative : '不满意',
   					}
   				}
   			}
   		},
   		// data(){
   		// 	return {
   		// 		// 因为子组件不能直接修改父组件 所以我们要在data里面声明一个新的变量来接收一下
   		// 		selectType2 : this.selectType ,
   		// 		onlyContent2 : this.onlyContent
   		// 	}
   		// },
   		methods : {
   			// 切换评价列表
   			select( type , e ){
   				// console.log(type)
   				if ( !e._constructed ) {
   					return;
   				};
   				this.selectType2 = type;
   				bus.$emit('selectType-click',type);
   			},
   			// 只看有内容的评价
   			toggleContent(e){
   				if ( !e._constructed ) {
   					return;
   				};
   				// this.onlyContent2 = !this.onlyContent2;
   				bus.$emit('onlyContent-click',this.onlyContent2);
   			}
   		}
   	}
</script>

<style lang="less" type="text/less">
	
	.ratingselect{
		.rating-type{
			padding: 0.36rem;
			margin: 0 0.36rem;
			border-bottom: 1px solid rgba(7,17,27,0.2);
			font-size: 0;
			.block{
				display: inline-block;
				padding: 0.16rem 0.24rem;
				margin-right: 0.16rem;
				border-radius: 0.02rem;
				color:rgb(77,85,93);
				font-size: 0.24rem;
				&.active{
					color:#fff;
				}
				.count{
					margin-left: 0.04rem;
					line-height: 0.32rem;
					font-size: 0.16rem;
				}
				&.positive{
					background: rgba(0,160,220,0.2);
					&.active{
						background: rgb(0,160,220);
					}
				}
				&.negative{
					background: rgba(77,85,93,0.2);
					&.active{
						background: rgb(77,85,93);
					}
				}
			}
		}
		.switch{
			padding: 0.24rem 0.36rem;
			line-height: 0.48rem;
			border-bottom: 1px solid rgba(7,17,27,0.2);
			color:rgb(147,153,159);
			&.on{
				.cc{
					i{
						color:#00c580; 
					}
				}
			}
			.cc{
				i{
					font-size: 0.48rem;
					color:rgb(147,153,159);
					line-height: 0.48rem;
				}
			}
			.title{
				font-size: 0.28rem;
				color: rgb(147,153,159);
				line-height: 0.48rem;
				margin-left: 0.08rem;
			}
		}
	}
</style>



