<template>
    <transition name = 'moveleft'>
        <div class="detail" v-show = 'showDetail' ref='food'>
            <div class="detail-content">
                <!-- 详情页头部图片 -->
                <div class="image-header">
                    <img :src="food.image" alt="">
                    <div class="back" @click = 'back'>
                        <i class="icon iconfont icon-shiliangzhinengduixiang4"></i>
                    </div>
                </div>
                <!-- 详情文字 -->
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>
                    <div class="details">
                        <span class="sell-count">月售{{food.sellCount}}份</span>
                        <span class="rating">好评率{{food.rating}}</span>
                    </div>
                    <!-- 价格 -->
                    <div class="price">
                        <span class="now">￥{{food.price}}</span>
                        <span v-show = 'food.oldPrice' class="old">￥{{food.oldPrice}}</span>
                    </div>
                    <!-- 购物车组件 -->
                    <div class="cartcontrol-warp">
                        <cartcontrol :food = 'food'></cartcontrol>
                    </div>
                    <div @click = 'addFirst( $event )' class="buy" v-show = ' !food.count || food.count === 0 '>加入购物车</div>
                </div>
                <!-- 分割线 -->
                <split></split>
                <!-- 商品信息简介 -->
                <div class="info" v-show = 'food.info'>
                    <div class="title">商品信息</div>
                    <div class="text">{{food.info}}</div>
                </div>
                <!-- 分割线 -->
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <!-- 商品选择展示的组件 -->
                    <ratingselect :select-type = 'selectType' :only-content = 'onlyContent' :desc = 'desc' :ratings = 'food.ratings'></ratingselect>
                    <!-- 评论详情 -->
                    <div class="rating-warp">
                        <ul v-show = 'food.ratings && food.ratings.length'>
                            <!-- v-show 绑定一个表达式  -->
                            <li v-show = 'needShow(rating.rateType , rating.text)' v-for = 'rating in food.ratings' class='rating-item'>
                                <!-- 客户信息 -->
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img :src="rating.avatar" class='avatar' width='24px' height='24px' alt="">
                                </div>
                                <div class="time">{{rating.rateTime | getTimes}}</div>
                                <div class="text">
                                    <span>
                                        <i v-show = 'rating.rateType === 0' class="icon iconfont icon-damuzhion cc"></i>
                                        <i v-show = 'rating.rateType === 1' class="icon iconfont icon-down dd"></i>
                                    </span>{{rating.text}}
                                </div>
                            </li>
                        </ul>
                        <div class="no-rating" v-show = '!food.ratings || !food.ratings.length'>暂无评价</div>
                    </div>
                </div>
            </div>
        </div>
    </transition>  
</template>

<script>

    import BScroll from 'better-scroll';
    import cartcontrol from '../cartcontrol/cartcontrol.vue';
    import Vue from 'vue';
    // 分割线
    import split from '../split/split.vue';
    import ratingselect from '../ratingselect/ratingselect.vue';
    import bus from '../../../static/js/eventBus.js';
    //定义三个常量
    const POSITIVE = 0; // 正面评价
    const NEGATIVE = 1; // 负面评价
    const ALL = 2;      // 所有评价 


    export default {

        created (){
            bus.$on('selectType-click', type => {
                this.selectType = type;
                // console.log(type)
                this.$nextTick( () => {
                    // this.scroll.refresh();
                })
            });

            bus.$on('onlyContent-click', cc => {
                // console.log(onlyContent)
                this.onlyContent = cc;
                this.$nextTick( () => {
                    // this.scroll.refresh();
                })
            })
        },
        filters : {
            getTimes(date){
                var time = new Date(date);
                var y = time.getFullYear();//年
                var m = time.getMonth() + 1;//月
                var d = time.getDate();//日
                var h = time.getHours();//时
                var mm = time.getMinutes();//分
                var s = time.getSeconds();//秒
                return (y+"-"+m+"-"+d+" "+h+":"+mm+":"+s);
            }
        },
        props : {
            food : {
                type : Object
            }
        },
        data() {
            return {
                // 详情默认false不显示
                showDetail : false,
                selectType : ALL,
                onlyContent : true,
                desc : {
                    all : '全部',
                    positive : '推荐',
                    negative : '吐槽',
                }
            };
        },
        components : {
            cartcontrol ,
            split ,
            ratingselect
        },
        methods : {
            // 设计组件方法里面 命名规范 也是编程习惯 可以被外部调用 直接命名 如果是私有的 就加一个_(下划线)
            show(){
                // 再次初始化一下
                this.selectType = ALL;
                this.onlyContent = false;
                this.showDetail = true;
                this.$nextTick( () => {
                    if( !this.scroll ){
                        this.scroll = new BScroll( this.$refs.food ,{
                            click : true
                        });
                    }else{
                        this.scroll.refresh();
                    }
                });
            },
            // 隐藏详情页
            back(){
                this.showDetail = false;
            },
            // 第一次添加购物车
            addFirst(e){
                // console.log(4)
                if( !e._constructed ){
                    return;
                }
                Vue.set( this.food , 'count' , 1 );
            },
            needShow(type,text){
                // 如果判断 onlyContent（只有内容）  并且没有文字评论 return false
                // 意思就是想显示内容 但是又没有内容 肯定返回false
                // 否则他显示所有的
                if( this.onlyContent && !text ){
                    return false;
                }
                // 如果当前的类型是所有的 那么就显示
                if( this.selectType === ALL ){
                    return true;
                    this.$nextTick( () => {
                        this.scroll.refresh();
                    })
                }else{
                    // 否则 判断当前的type 是不是和selectType相等
                    // 如果相等 return true 否则 return false
                    return type === this.selectType;
                    this.$nextTick( () => {
                        this.scroll.refresh();
                    })
                }
            }
        }
    };
</script>

<style lang="less" type="text/less">
    // 商品详情进入动画
    .moveleft-enter-active, .moveleft-leave-active {
        transition: all 0.5s;
    }
    .moveleft-enter, .moveleft-leave-active {
        transform:translateX(100%); 
    }
    .detail{
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0.96rem;
        z-index: 5;
        width: 100%;
        height: auto;
        background: #fff;
        .detail-content{
            .image-header{
                position: relative;
                width: 100%;
                height: 0;
                // 当给这个div设置padding为100%，也就是宽和高都一样 宽高相等
                padding-bottom: 100%;
                img{
                    position: absolute;
                    top: 0;
                    left:0;
                    width: 100%;
                    height: 100%;
                }
                .back{
                    position: absolute;
                    top:0.15rem;
                    left:0.15rem;
                    i{
                        padding: 0.15rem;
                        font-size: 0.2rem;
                        display: block;
                        color: white;
                    }
                }
            }
            .content{
                position: relative;
                padding: 0.36rem;
                .title{
                    line-height: 0.28rem;
                    margin-bottom: 0.16rem;
                    color:rgb(7,17,27);
                    font-size: 0.28rem;
                    font-weight: 700;
                }
                .details{
                    padding-bottom: 0.36rem;
                    color:rgb(147,153,159);
                    line-height: 0.2rem;
                    font-size: 0.2rem;
                    .rating{
                        margin-left: 0.24rem;
                    }
                }
                .price{
                    line-height: 0.48rem;   
                    .now{
                        font-size: 0.28rem;
                        font-weight: normal;
                        color: rgb(240,2,20);
                    }
                    .old{
                        font-size: 0.2rem;
                        font-weight: 700;
                        color:rgb(147,153,159);
                        text-decoration: line-through;
                        margin-left: 0.2rem;
                    }
                }
                .cartcontrol-warp{
                    position: absolute;
                    right: 0.24rem;
                    bottom: 0.24rem;
                }
                .buy{
                    position: absolute; 
                    right: 0.36rem;
                    bottom: 0.36rem;
                    width: 1.48rem;
                    height: 0.48rem;
                    z-index: 10;
                    line-height: 0.48rem;
                    padding: 0 0.24rem;
                    box-sizing: border-box;
                    font-size: 0.2rem;
                    border-radius: 0.24rem;
                    color:#fff;
                    background:rgb(0,160,220);
                }
            }
        }
        .info{
            padding: 0.36rem;
            .title{
                line-height: 0.28rem;
                margin-bottom: 0.12rem;
                font-size: 0.28rem;
                color:rgb(7,17,27);
            }
            .text{
                line-height: 0.48rem;
                padding: 0 0.16rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(77,85,93);
            }
        }
        .rating{
            padding-top: 0.36rem;
            .title{
                line-height: 0.28rem;
                margin-left: 0.36rem;
                font-size: 0.28rem;
                color: rgb(7,17,27);
            }
            .rating-warp{
                padding: 0 0.36rem;
                .rating-item{
                    position: relative;
                    padding: 0.32rem 0;
                    border-bottom: 1px solid rgba(7,17,27,0.1);
                    .user{
                        position: absolute;
                        right:0;
                        top:0.32rem;
                        font-size: 0;
                        line-height: 0.24rem;
                        .name{
                            display: inline-block;
                            vertical-align: top;
                            font-size: 0.3rem;
                            margin-right: 0.12rem;
                            color:rgb(147,153,159);
                        }
                        .avatar{
                            border-radius: 50%;
                        }
                    }
                    .time{
                        line-height: 0.24rem;
                        font-size: 0.2rem;
                        color:rgb(147,153,159);
                        margin-bottom: 0.12rem;
                    }
                    .text{
                        line-height: 0.32rem;
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        span{
                            i{
                                margin-right: 0.08rem;
                                line-height: 0.32rem;
                                font-size: 0.24rem;
                                &.cc{
                                    color:rgb(0,160,220);
                                }
                                &.dd{
                                    color:rgb(147,153,159);
                                }
                            }
                        }
                    }
                }
                .no-rating{
                    padding: 0.32rem 0;
                    font-size: 0.24rem;
                    color:rgb(147,153,159);
                }
            }
        }
    }
</style>



